.App {
  position: relative;
  width: 100vw;
  height: 100vh;
  /* overflow: hidden; */
  background: #eaeaea;
  font-family: Helvetica;
}

.app-body {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: row;
}

.clickBtn {
  background: #4f5e75;
  cursor: pointer;
}

.clickBtn:active {
  background:darkseagreen;
}
